<template>
  <div v-if="isOwner_ygg" class="ygg-operator-box">
    <el-row gutter="5">
      <el-col :span="3" class="ygg-o-label">质押代币：</el-col>
      <el-col :span="9">
        <el-input v-model="tokenDeposit" placeholder="erc20 address" />
      </el-col>
      <el-col :span="3" class="ygg-o-label">收款：</el-col>
      <el-col :span="9">
        <el-input v-model="coldAccount" placeholder="cold wallet address" />
      </el-col>
    </el-row>
    <el-row gutter="5">
      <el-col :span="3" class="ygg-o-label">奖励代币：</el-col>
      <el-col :span="9">
        <el-input v-model="tokenReward" placeholder="erc20 address" />
      </el-col>
      <el-col :span="3" class="ygg-o-label">放款：</el-col>
      <el-col :span="9">
        <el-input v-model="hotAccount" placeholder="hot wallet address" />
      </el-col>
    </el-row>
    <el-row gutter="5">
      <el-col :span="3" class="ygg-o-label">分享奖励代币：</el-col>
      <el-col :span="9">
        <el-input v-model="tokenRewardForShare" placeholder="erc20 address" />
      </el-col>
      <el-col :span="3" class="ygg-o-label">分享放款：</el-col>
      <el-col :span="9">
        <el-input
          v-model="hotAccountForShare"
          placeholder="hot wallet address"
        />
      </el-col>
    </el-row>
    <el-row gutter="5">
      <el-col :span="3"></el-col>
      <el-col :span="19">
        <el-button
          type="primary"
          :loading="isPoolAdding_ygg"
          @click.prevent="addPool"
          >创建标的[池子]</el-button
        >
      </el-col>
    </el-row>
    <div class="ygg-owner-oprat-box">
      <!-- add operator -->
      <el-row>
        <el-col :span="3" style="text-align:right;">交易员地址：</el-col>
        <el-col :span="9">
          <el-input
            ref="addrOfOpt"
            placeholder="operator address"
            v-model="addressOfOperator"
          ></el-input>
        </el-col>
        <el-col :span="3">
          <el-button
            type="primary"
            :loading="isAddingOperator_ygg"
            @click.prevent="addOperator"
            >添加</el-button
          >
        </el-col>
        <el-col :span="9">
          <font style="color:grey;"
            >交易员职能：发放打金奖励，分享奖励；审批赎回申请；修改打金总量，APY值；</font
          >
        </el-col>
      </el-row>
      <!-- list operator[includ delete operator btn] -->
      <div style="padding:0px 50px;">
        <el-table :data="operators_ygg">
          <el-table-column label="address">
            <template #default="scope">
              {{ scope.row }}
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template #default="scope">
              <el-button
                type="primary"
                :loading="isDeletingOperator_ygg"
                @click.prevent="deleteOperator(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div style="width:100%;height:10px;"></div>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { mapGetters } from "vuex";
export default {
  name: "add pool",
  data: function() {
    return {
      // pool
      tokenDeposit: "",
      coldAccount: "",
      tokenReward: "",
      hotAccount: "",
      tokenRewardForShare: "",
      hotAccountForShare: "",
      // operator
      addressOfOperator: "",
    };
  },
  computed: {
    ...mapGetters([
      "isPoolAdding_ygg",
      "isOwner_ygg",
      "isEthAddressGeted",
      "isAddingOperator_ygg",
      "isDeletingOperator_ygg",
      "operators_ygg",
    ]),
  },
  watch: {
    isEthAddressGeted: async function(val) {
      if (val) {
        try {
          this.$store.dispatch("ygg/getOwner");
          this.$store.dispatch("ygg/getOperators");
        } catch (err) {
          console.log(err);
          ElMessage.error(err);
        }
      }
    },
  },
  methods: {
    async deleteOperator(addressOfOprat) {
      await this.$store
        .dispatch("ygg/setOperators", {
          addressOfOperator: addressOfOprat,
          isOperator: false,
        })
        .then(() => {
          ElMessage.success("删除成功");
          return;
        })
        .catch((err) => {
          ElMessage.warning(`删除失败,err=${err}`);
          return;
        });
    },
    async addOperator() {
      if (this.addressOfOperator == "") {
        ElMessage.warning("输入操作员地址");
        this.$refs.addrOfOpt.focus();
        return;
      }
      await this.$store
        .dispatch("ygg/setOperators", {
          addressOfOperator: this.addressOfOperator,
          isOperator: true,
        })
        .then(() => {
          ElMessage.success("添加成功");
          return;
        })
        .catch((err) => {
          ElMessage.warning(`添加失败,err=${err}`);
          return;
        });
    },
    async addPool() {
      if (
        this.tokenDeposit == "" ||
        this.coldAccount == "" ||
        this.tokenReward == "" ||
        this.hotAccount == "" ||
        this.tokenRewardForShare == "" ||
        this.hotAccountForShare == ""
      ) {
        ElMessage.warning("输入有空值");
        return;
      }
      const pool = {
        tokenDeposit: this.tokenDeposit,
        tokenReward: this.tokenReward,
        tokenRewardForShare: this.tokenRewardForShare,
        coldAccount: this.coldAccount,
        hotAccount: this.hotAccount,
        hotAccountForShare: this.hotAccountForShare,
      };
      await this.$store
        .dispatch("ygg/addPool", { pool })
        .then((msg) => {
          ElMessage.success(msg);
        })
        .catch((err) => {
          ElMessage.error(err);
        });
    },
  },
};
</script>

<style scoped>
.ygg-owner-oprat-box {
  border: solid 1px grey;
  margin: 10px 10px;
  padding: 10px;
}
.el-row {
  padding-top: 10px;
  padding-bottom: 10px;
}
.el-col {
  border-radius: 4px;
}
.ygg-operator-box {
  text-align: left;
  background: white;
}
.ygg-o-label {
  text-align: right;
  line-height: 32px;
}
</style>
