export default {
  namespaced: true,
  state: {
    isHadHero: false,
    isLoadingHero: false,
    loadingProcessText: "0:start",
    balance: 0,
    isTokenIdFetched_warrior: false,
    amountFetchDetailErr: 0,
  },
  mutations: {
    SET_IS_HAD_HERO: (state, h) => {
      state.isHadHero = h;
    },
    SET_IS_LOADING_HERO: (state, s) => {
      state.isLoadingHero = s;
    },
    SET_LOADING_PROCESS_TEXT: (state, s) => {
      state.loadingProcessText = s;
    },
    SET_BALANCE: (state, b) => {
      state.balance = b;
    },
    SET_IS_TOKEN_ID_FETCHED_WARRIOR: (state, f) => {
      state.isTokenIdFetched_warrior = f;
    },
    ADD_AMOUNT_FETCH_DETAIL_ERR: (state) => {
      state.amountFetchDetailErr = state.amountFetchDetailErr + 1;
    },
    CLEAR_AMOUNT_FETCH_DETAIL_ERR: (state) => {
      state.amountFetchDetailErr = 0;
    },
  },
  actions: {
    async search(
      { rootGetters, rootState, state, dispatch, commit },
      { owner }
    ) {
      if (state.isLoadingHero) return;
      console.log("====search owner=", owner);
      commit("SET_IS_LOADING_HERO", true);
      commit("SET_LOADING_PROCESS_TEXT", `1-0:开始获取战士tokenId列表[0]`);
      // clear tokenIds and hero detail
      commit("role_detail/CLEAR_HEROS", {}, { root: true });
      commit("role_warrior/CLEAR_TOKEN_IDS", {}, { root: true });
      commit("role_robber/CLEAR_TOKEN_IDS", {}, { root: true });
      commit("role_mage/CLEAR_TOKEN_IDS", {}, { root: true });
      commit("role_yx/CLEAR_TOKEN_IDS", {}, { root: true });
      commit("mining/CLEAR_TOKEN_IDS", {}, { root: true });
      commit("mining/CLEAR_TOKEN_IDS_NEW", {}, { root: true });
      commit("CLEAR_AMOUNT_FETCH_DETAIL_ERR");

      await dispatch("role_warrior/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-1:获得战士tokenId列表[${rootGetters.balanceOfWarrior}]`
      );
      await dispatch("role_robber/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-2:获得盗贼tokenId列表[${rootGetters.tbalanceOfRobber}]`
      );
      await dispatch("role_mage/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-3:获得法师tokenId列表[${rootGetters.balanceOfMage}]`
      );
      await dispatch("role_yx/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-4:获得游侠tokenId列表[${rootGetters.balanceOfYx}]`
      );
      // query ming info
      await dispatch("mining/getMingingInfoAll", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-5:获得打工中角色tokenId列表old[${rootState.mining.balanceOf}],new[${rootState.mining.balanceOf_new}]`
      );
      const balance =
        parseInt(rootGetters.balanceOfWarrior) +
        parseInt(rootGetters.tbalanceOfRobber) +
        parseInt(rootGetters.balanceOfMage) +
        parseInt(rootGetters.balanceOfYx) +
        parseInt(rootState.mining.balanceOf) +
        parseInt(rootState.mining.balanceOf_new);
      commit("SET_BALANCE", balance);
      if (balance == 0) {
        commit("SET_IS_HAD_HERO", false);
        commit("SET_IS_LOADING_HERO", false);
        return;
      }
      commit("SET_IS_HAD_HERO", true);
      // await dispatch("role_detail/getRoleDetail", {}, { root: true });
      commit("SET_LOADING_PROCESS_TEXT", `3-0:fetching hero detail`);
      // commit("SET_IS_LOADING_HERO", false);
    },
    async tryFinishLoadingHeros({ rootGetters, rootState, state, commit }) {
      const balance =
        parseInt(rootGetters.balanceOfWarrior) +
        parseInt(rootGetters.tbalanceOfRobber) +
        parseInt(rootGetters.balanceOfMage) +
        parseInt(rootGetters.balanceOfYx) +
        parseInt(rootState.mining.balanceOf) +
        parseInt(rootState.mining.balanceOf_new);
      console.log(
        `tryFinishLoadingHeros balance=${balance} heros=${rootState.role_detail.heros.length} detail fetch err=${state.amountFetchDetailErr}`
      );
      if (
        balance ==
        rootState.role_detail.heros.length + state.amountFetchDetailErr
      ) {
        commit("SET_IS_LOADING_HERO", false);
      }
    },
  },
};
