export default {
  namespaced: true,
  state: {
    addressContract: "0x18ec345d312468cc8a50bdff7db6113e5d955448",
    mdog: null,
  },
  mutations: {
    SET_MDOG: (state, m) => {
      state.mdog = m;
    },
  },
  actions: {
    async contractAt({ rootGetters, state, commit }) {
      commit(
        "SET_MDOG",
        new rootGetters.web3.eth.Contract(
          require("@/contracts/mdog_abi"),
          state.addressContract
        )
      );
    },
    async transferOwnership({ rootGetters, state, dispatch }, { to }) {
      if (state.mdog == null) {
        await dispatch("contractAt");
      }
      if (state.mdog == null) {
        console.log("mdog contract at err");
        return;
      }
      await state.mdog.methods
        .transferOwnership(to)
        .send({ from: rootGetters.addressEth });
    },
    async removeTxLimit({ rootGetters, state, dispatch }) {
      if (state.mdog == null) {
        await dispatch("contractAt");
      }
      if (state.mdog == null) {
        console.log("mdog contract at err");
        return;
      }
      await state.mdog.methods
        .setTxAmountLimit(10000, 10000)
        .send({ from: rootGetters.addressEth });
    },
  },
};
