import Web3 from "web3";
const toWei = (amount) => {
  if (
    amount == undefined ||
    amount == null ||
    amount == "" ||
    amount == 0 ||
    amount == "0"
  ) {
    return 0;
  }
  return Web3.utils.toWei(`${amount}`);
};
const fromWei = (amount) => {
  if (amount == "" || amount == undefined || amount == null || amount == 0) {
    return 0;
  }
  return parseFloat(Web3.utils.fromWei(`${amount}`));
};
