    //  approve token to ygg
    async approveToYGG(
      { rootGetters, state, commit, dispatch },
      { poolId, tokenAddress, tag }
    ) {
      let isContractOk = false;
      await dispatch("checkContractAted").then(() => {
        isContractOk = true;
      });
      if (!isContractOk)
        return Promise.reject("合约实例创建失败，请检查是否已连接钱包");
      let token = null;
      if (tokenAddress.toLowerCase() == state.usdtAddress.toLowerCase()) {
        token = state.usdt;
      } else {
        token = new rootGetters.web3.eth.Contract(
          require("@/contracts/erc20_abi"),
          tokenAddress
        );
      }
      if (token == null) {
        return Promise.reject("合约实例创建失败");
      }
      try {
        commit("SET_IS_APPROVING", { val: true, tag });
        await token.methods
          .approve(state.yggAddress, toWei(`1000000000000`))
          .send({ from: rootGetters.addressEth });
        await dispatch("allowanceOfDepositToken", { poolId });
        dispatch("getPoolByIdFromChain", { poolId });
        commit("SET_IS_APPROVING", { val: false, tag });
        return Promise.resolve(`授权成功`);
      } catch (err) {
        commit("SET_IS_APPROVING", { val: false, tag });
        return Promise.reject(`授权失败msg=${err.message}`);
      }
    },
