<template>
  <div v-if="isShow_">
    <el-dialog v-model="isShow_" title="质押" :width="dialogWidth" center>
      <div>
        <el-row>
          <el-col :xs="5" :sm="3" class="ygg-d-amount-text">数量:</el-col>
          <el-col :xs="12" :sm="9"
            ><el-input-number
              ref="amountOfD"
              v-model="amountOfDeposit"
              :min="0"
              :step="step"
              :max="max"
              precision="2"
              controls-position="right"
              style="width:150px;"
              @focus="onInputFocus"
              @blur="onInputBlur"
              placeholder="amount"
            ></el-input-number>
          </el-col>
          <el-col :xs="7" :sm="6">
            <el-button type="text" @click="onMaxAmount">MAX</el-button>
          </el-col>
          <el-col :xs="0" :sm="6"></el-col>
        </el-row>
        <el-row>
          <el-col :xs="5" :sm="3" class="ygg-d-amount-text">
            余额:
          </el-col>
          <el-col :xs="19" :sm="21" class="ygg-d-balance">
            {{ fromWei(balanceOfDepositToken_ygg) }}
            {{ symbolOfDepositToken_ygg }}
          </el-col>
        </el-row>
        <el-row v-if="!isHadSharer_ygg">
          <el-col :xs="5" :sm="3" class="ygg-d-amount-text">
            分享人:
          </el-col>
          <el-col :xs="17" :sm="19">
            <el-input
              :disabled="isUrlWithSharer"
              v-model="addressOfSharer"
              placeholder="optianl shared from address"
            ></el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <el-button @click.prevent="isShow_ = !isShow_">取消</el-button>
        <el-button
          v-if="isHadApprove"
          @click.prevent="onDeposit"
          type="primary"
          :loading="isDepositing_ygg"
          >确定</el-button
        >
        <el-button
          v-else
          type="primary"
          @click.prevent="onApprove"
          :loading="isApproving_ygg"
          >授权</el-button
        >
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ElMessage, ElMessageBox } from "element-plus";
import { mapGetters } from "vuex";
export default {
  name: "deposit",
  props: {
    poolId: {
      type: Number,
      default: 0,
    },
    isShow: {
      type: Boolean,
      default: false,
    },
  },
  emits: {
    close: null,
  },
  computed: {
    ...mapGetters([
      "web3",
      "pool_ygg",
      "allowanceOfDepositToken_ygg",
      "balanceOfDepositToken_ygg",
      "symbolOfDepositToken_ygg",
      "isDepositing_ygg",
      "isApproving_ygg",
      "isHadSharer_ygg",
      "addressOfSharer_ygg",
    ]),
    pool: function() {
      return this.pool_ygg(this.poolId);
    },
    step: function() {
      return Math.floor(this.fromWei(this.balanceOfDepositToken_ygg) / 5);
    },
    max: function() {
      return this.fromWei(this.balanceOfDepositToken_ygg);
    },
    isHadApprove: function() {
      return this.fromWei(this.allowanceOfDepositToken_ygg) > 0;
    },
    saddr: function() {
      return this.$route.query.saddr;
    },
    dialogWidth: function() {
      return window.screen.width <= 768 ? "90%" : "500px";
    },
  },
  watch: {
    poolId: function(val) {
      //   console.log("deposit dialog poolId=", val);
      if (val >= 0) {
        this.$store.dispatch("ygg/balanceOfDepositToken", { poolId: val });
        this.$store.dispatch("ygg/allowanceOfDepositToken", { poolId: val });
        this.$store.dispatch("ygg/getSharer");
      }
    },
    isShow: function(val) {
      this.isShow_ = val;
    },
    isShow_: function(val) {
      if (!val) {
        this.$emit("close", { isShow: val });
      } else {
        this.$store.dispatch("ygg/balanceOfDepositToken", {
          poolId: this.poolId,
        });
        this.$store.dispatch("ygg/allowanceOfDepositToken", {
          poolId: this.poolId,
        });
        this.amountOfDeposit = 0;
        console.log("saddr=", this.saddr);
        this.addressOfSharer = this.saddr;
        if (this.addressOfSharer == undefined) {
          this.isUrlWithSharer = false;
        } else {
          this.isUrlWithSharer = true;
        }
        this.isHadShowSharerTip = false;
      }
    },
  },
  data: function() {
    return {
      isShow_: false,
      amountOfDeposit: 0,
      addressOfSharer: "",
      isUrlWithSharer: false,
      isHadShowSharerTip: false,
    };
  },
  methods: {
    onInputFocus: function() {
      if (this.amountOfDeposit == 0) {
        this.amountOfDeposit = undefined;
      }
    },
    onInputBlur: function() {
      if (this.amountOfDeposit == undefined) {
        this.amountOfDeposit = 0;
      }
    },
    onMaxAmount: function() {
      this.amountOfDeposit = this.fromWei(this.balanceOfDepositToken_ygg);
    },
    onApprove: async function() {
      await this.$store
        .dispatch("ygg/approveToYGG", {
          poolId: this.poolId,
          tokenAddress: this.pool.tokenDeposit,
          tag: 1,
        })
        .then(() => {
          ElMessage.success("授权成功");
        })
        .catch((err) => {
          ElMessage.error(err);
        });
    },
    onDeposit: async function() {
      if (
        this.amountOfDeposit <= 0 ||
        this.amountOfDeposit > this.fromWei(this.balanceOfDepositToken_ygg)
      ) {
        ElMessage.warning("质押数量错误");
        this.$refs.amountOfD.focus();
        return;
      }
      console.log(
        "isHadSharer_ygg=",
        this.isHadSharer_ygg,
        this.addressOfSharer,
        this.addressOfSharer_ygg
      );
      if (this.isHadSharer_ygg) {
        this.addressOfSharer = this.addressOfSharer_ygg;
        console.log(this.addressOfSharer, this.addressOfSharer_ygg);
      } else {
        if (this.isUrlWithSharer) {
          this.addressOfSharer = this.saddr;
        } else {
          if (!this.isHadShowSharerTip) {
            ElMessageBox.alert(
              "请填写推荐地址，如未获得推荐地址请忽略",
              "提示",
              {
                confirmButtonText: "OK",
                callback: () => {
                  this.isHadShowSharerTip = true;
                },
              }
            );
            return;
          }
        }
      }

      await this.$store
        .dispatch("ygg/depositByUser", {
          poolId: this.poolId,
          amountByEth: this.amountOfDeposit,
          addressOfSharer: this.addressOfSharer,
        })
        .then(() => {
          ElMessage.success("质押成功");
          this.isShow_ = false;
          return;
        })
        .catch((err) => {
          ElMessage.warning(err);
          return;
        });
    },
    fromWei: function(value) {
      if (
        value == null ||
        value == undefined ||
        value == "" ||
        value == 0 ||
        this.web3 == null
      ) {
        return 0;
      }
      return (
        (parseFloat(this.web3.utils.fromWei(`${value}`)).toFixed(4) * 10000) /
        10000
      );
    },
  },
};
</script>

<style scoped>
.ygg-d-amount-text {
  text-align: right;
  line-height: 32px;
  padding-right: 10px;
}
.ygg-d-balance {
  line-height: 32px;
}
.el-row {
  padding: 5px 0px;
}
</style>
