    //         if(!state.isContractAted){
    //             return Promise.reject('contract at err')
    //         }
    //     }
    //     if (poolId < 0 || poolId > state.pools.length - 1) {
    //         return Promise.reject(`poolId[${poolId}] 参数异常`);
    //     }
    //     let req = state.reqD
    //     let doing_mutation_name = 'SET_IS_ADDING_REQ_D'
    //     let addressOfSharer = "0x0000000000000000000000000000000000000000"

    //     if(isReqOfDeposit){
    //         if(!(sharer==null||sharer==undefined||sharer=="")){
    //             addressOfSharer = sharer
    //         }
    //     }else{
