<template>
  <div v-if="isShow_">
    <el-dialog v-model="isShow_" title="赎回" :width="dialogWidth" center>
      <div>
        <el-row>
          <el-col :xs="5" :sm="3" class="ygg-d-amount-text">数量:</el-col>
          <el-col :xs="12" :sm="9"
            ><el-input-number
              ref="amountOfW"
              v-model="amountOfWithdraw"
              :min="0"
              :step="step"
              :max="max"
              precision="2"
              controls-position="right"
              style="width:150px;"
              @blur="onInputBlur"
              @focus="onInputFocus"
              placeholder="amount"
            ></el-input-number>
          </el-col>
          <el-col :xs="7" :sm="6">
            <el-button type="text" @click="onMaxAmount">MAX</el-button>
          </el-col>
          <el-col :xs="0" :sm="6"> </el-col>
        </el-row>
        <el-row>
          <el-col :xs="5" :sm="3" class="ygg-d-amount-text">
            可赎回:
          </el-col>
          <el-col :xs="19" :sm="21" class="ygg-d-balance">
            {{ fromWei(pool.amountOfDeposited) }}
            {{ symbolOfDepositToken_ygg }},
            {{ amountInAudit }}
            {{ symbolOfDepositToken_ygg }}
            审核中
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <el-button @click.prevent="isShow_ = !isShow_">取消</el-button>
        <el-button
          type="primary"
          :loading="isWithdrawing_ygg"
          @click.prevent="onWithdraw"
          >确定</el-button
        >
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { mapGetters } from "vuex";
export default {
  name: "withdraw",
  props: {
    poolId: {
      type: Number,
      default: 0,
    },
    isShow: {
      type: Boolean,
      default: false,
    },
    emits: {
      close: null,
    },
  },
  computed: {
    ...mapGetters([
      "web3",
      "pool_ygg",
      "pool_req_deposit_ygg",
      "pool_req_withdraw_ygg",
      "symbolOfDepositToken_ygg",

      "isWithdrawing_ygg",
    ]),
    pool: function() {
      return this.pool_ygg(this.poolId);
    },
    reqD: function() {
      return this.pool_req_deposit_ygg(this.poolId);
    },
    reqW: function() {
      return this.pool_req_withdraw_ygg(this.poolId);
    },
    step: function() {
      return Math.floor(this.fromWei(this.pool.amountOfDeposited) / 5);
    },
    max: function() {
      return this.fromWei(this.pool.amountOfDeposited);
    },
    amountInAudit: function() {
      return (
        parseFloat(this.fromWei(`${this.reqD.balanceTotalsByUser}`)) +
        parseFloat(this.fromWei(`${this.reqW.balanceTotalsByUser}`))
      );
    },
    dialogWidth: function() {
      return window.screen.width <= 768 ? "90%" : "500px";
    },
  },
  watch: {
    poolId: function(val) {
      //   console.log("deposit dialog poolId=", val);
      if (val >= 0) {
        this.$store.dispatch("ygg/balanceOfDepositToken", { poolId: val });
      }
    },
    isShow: function(val) {
      this.isShow_ = val;
    },
    isShow_: function(val) {
      if (!val) {
        this.$emit("close", { isShow: val });
      }
    },
  },
  data: function() {
    return {
      isShow_: false,
      amountOfWithdraw: 0,
    };
  },
  methods: {
    onInputFocus: function() {
      if (this.amountOfWithdraw == 0) {
        this.amountOfWithdraw = undefined;
      }
    },
    onInputBlur: function() {
      if (this.amountOfWithdraw == undefined) {
        this.amountOfWithdraw = 0;
      }
    },
    onWithdraw: async function() {
      if (
        this.amountOfWithdraw <= 0 ||
        this.amountOfWithdraw > this.fromWei(this.pool.amountOfDeposited)
      ) {
        ElMessage.warning("赎回数量错误");
        this.$refs.amountOfW.focus();
        return;
      }
      await this.$store
        .dispatch("ygg/withdrawByUser", {
          poolId: this.poolId,
          amount: this.amountOfWithdraw,
        })
        .then(() => {
          ElMessage.success("赎回申请成功");
          this.isShow_ = false;
          return;
        })
        .catch((err) => {
          ElMessage.warning(err);
          return;
        });
    },
    onMaxAmount: function() {
      this.amountOfWithdraw = this.fromWei(this.pool.amountOfDeposited);
    },
    fromWei: function(value) {
      if (
        value == null ||
        value == undefined ||
        value == "" ||
        value == 0 ||
        this.web3 == null
      ) {
        return 0;
      }
      return (
        (parseFloat(this.web3.utils.fromWei(`${value}`)).toFixed(4) * 10000) /
        10000
      );
    },
  },
};
</script>

<style scoped>
.ygg-d-amount-text {
  text-align: right;
  line-height: 32px;
  padding-right: 10px;
}
.ygg-d-balance {
  line-height: 32px;
}
.el-row {
  padding: 5px 0px;
}
</style>
