<template>
  <div style="background-color:black;padding:10px 10px;">
    <div>
      <el-button
        type="primary"
        :loading="
          isDeposit ? isDepositingByOperator_ygg : isWithdrawingByOperator_ygg
        "
        @click.prevent="batchAudit"
        >一键审批{{ isDeposit ? "入金" : "出金" }}</el-button
      >
      <el-button @click.prevent="refreshPool">刷新</el-button>
    </div>
    <div>
      <el-table
        :data="reqs"
        @selection-change="handleSelectionChange"
        :row-class-name="tableRowClassName"
        :default-sort="{ prop: 'height', order: 'descending' }"
      >
        <el-table-column type="selection" width="40" />
        <el-table-column label="序号" width="50">
          <template #default="scope">
            {{ scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column
          label="是否审批"
          width="80"
          :filters="[
            { text: '是', value: true },
            { text: '否', value: false },
          ]"
          :filter-method="filterHandle"
        >
          <template #default="scope">
            {{ scope.row.isCompleted ? "是" : "否" }}
          </template>
        </el-table-column>
        <el-table-column label="申请人">
          <template #default="scope">
            <!-- {{ addressShorter(scope.row.user) }} -->
            {{ scope.row.user }}
          </template>
        </el-table-column>
        <el-table-column :label="isDeposit ? '质押量' : '赎回量'">
          <template #default="scope">
            {{ fromWei(scope.row.amount) }}
          </template>
        </el-table-column>
        <el-table-column v-if="isDeposit" label="推荐人">
          <template #default="scope">
            <!-- {{ addressShorter(scope.row.sharer) }} -->
            {{ scope.row.sharer }}
          </template>
        </el-table-column>
        <el-table-column label="区块高度" sortable prop="height">
          <template #default="scope" width="40">
            {{ scope.row.height }}
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      layout="total,prev, pager, next"
      v-model:currentPage="currentPage"
      :total="total"
      :page-size="pool_req_page_size_ygg"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { mapGetters } from "vuex";
export default {
  name: "request of deposit or withdraw",
  props: {
    poolId: {
      type: Number,
      default: 0,
    },
    isDeposit: {
      type: Boolean,
      default: true,
    },
  },
  data: function() {
    return {
      multipleSelection: null,
      currentPage: 1,
    };
  },
  computed: {
    ...mapGetters([
      "pool_req_list_ygg",
      "pool_req_deposit_ygg",
      "pool_req_withdraw_ygg",
      "pool_req_page_size_ygg",
      "isEthAddressGeted",
      "web3",
      "isDepositingByOperator_ygg",
      "isWithdrawingByOperator_ygg",
    ]),
    reqs: function() {
      return this.pool_req_list_ygg(this.poolId, this.isDeposit);
    },
    reqD: function() {
      return this.pool_req_deposit_ygg(this.poolId);
    },
    reqW: function() {
      return this.pool_req_withdraw_ygg(this.poolId);
    },
    total: function() {
      console.log(
        "total=",
        this.isDeposit ? this.reqD.totalsByPool : this.reqW.totalsByPool
      );
      const total = this.isDeposit
        ? this.reqD.totalsByPool
        : this.reqW.totalsByPool;
      // this.total_ = total;
      return parseInt(total);
    },
    // currentPage: function() {
    //   return parseInt(this.isDeposit ? this.reqD.pageNum : this.reqW.pageNum);
    // },
  },
  // watch: {
  //   total: function(val, val_old) {
  //     console.log(`watch total val=${val}  val_old=${val_old}`);
  //     this.total_ = val;
  //   },
  //   isEthAddressGeted: async function(val) {
  //     console.log("watch isEthAddressGeted", val);
  //   },
  // },
  methods: {
    refreshPool: async function() {
      // await this.$store.dispatch("ygg/getPoolByIdFromChain", {
      //   poolId: this.poolId,
      // });
      await this.handleCurrentChange();
    },
    handleCurrentChange: async function() {
      // console.log(`handleCurrentChange val=${val}`);
      await this.$store.dispatch("ygg_req/getPagedReqs", {
        poolId: this.poolId,
        isReqOfDeposit: this.isDeposit,
        pageNum: this.currentPage - 1,
      });
    },
    batchAudit: async function() {
      console.log("batch audit ", this.multipleSelection);
      if (
        this.multipleSelection == null ||
        this.multipleSelection.length == 0
      ) {
        ElMessage.warning("未选中待审批申请");
        return;
      }
      let ids = [];
      let reqWs = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        const req = this.multipleSelection[i];
        if (req.isCompleted) {
          continue;
        }
        ids.push(req.id);
        if (!this.isDeposit) {
          reqWs.push({
            reqIndex: req.id,
            poolId: req.poolId,
            amount: req.amount,
            isOnlyComplete: false,
          });
        }
      }
      console.log(ids, reqWs);
      if (ids.length == 0) {
        ElMessage.warning("未选中待审批申请");
        return;
      }
      if (this.isDeposit) {
        await this.$store
          .dispatch("ygg/depositByOperator", { reqDs: ids })
          .then((msg) => {
            ElMessage.success(msg);
          })
          .catch((err) => {
            ElMessage.error(err);
          });
      } else {
        await this.$store
          .dispatch("ygg/withdrawByOperator", { reqWs })
          .then((msg) => {
            ElMessage.success(msg);
          })
          .catch((err) => {
            ElMessage.error(err);
          });
      }
    },
    handleSelectionChange: function(val) {
      this.multipleSelection = val;
    },
    filterHandle: function(val, row, col) {
      if (col["label"] == "是否审批") {
        return val == row.isCompleted;
      }
      return false;
    },
    tableRowClassName: function({ row }) {
      // console.log("tableRowClassName", row.isCompleted);
      if (row.isCompleted == undefined || row.isCompleted == false) {
        return "";
      } else {
        return "is-completed";
      }
    },
    // addressShorter: function(addr) {
    //   // const prefix = addr.slice(0, 6);
    //   // const trail = addr.slice(-4);
    //   return addr;
    //   // return `${addr.substr(0, 6)}...${addr.substr(-4, 4)}`;
    // },
    fromWei: function(value) {
      if (
        value == null ||
        value == undefined ||
        value == "" ||
        value == 0 ||
        this.web3 == null
      ) {
        return 0;
      }
      return (
        (parseFloat(this.web3.utils.fromWei(`${value}`)).toFixed(4) * 10000) /
        10000
      );
    },
  },
};
</script>

<style>
.el-table .is-completed {
  --el-table-tr-bg-color: #deeede;
  background-color: #deeede;
}
</style>
