<template>
  <div>
    <div
      id="copy-btn"
      ref="ref-copy-btn"
      :data-clipboard-text="surl"
      @click="copy"
    ></div>
    <div v-if="isShow_">
      <el-dialog v-model="isShow_" title="分享" :width="dialogWidth" center>
        {{ surl }}
        <el-input ref="inputOfUrl" v-model="copyText" v-show="false"></el-input>
        <template #footer>
          <el-button @click.prevent="isShow_ = !isShow_">取消</el-button>

          <el-button type="primary" @click.prevent="copyTrigle">复制</el-button>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ElMessage } from "element-plus";
import Clipboard from "clipboard";
export default {
  name: "share",
  props: {
    isShow: {
      type: Boolean,
      default: false,
    },
  },
  emits: {
    close: null,
  },
  data: function() {
    return {
      isShow_: false,
      copyText: "",
    };
  },

  computed: {
    ...mapGetters(["addressEth"]),
    surl: function() {
      //   console.log(window.location.origin);
      //   const domaim = "http://127.0.0.1:1024/";
      const url = `${window.location.origin}?saddr=${this.addressEth}`;
      console.log("share url is ", url);
      return url;
    },
    dialogWidth: function() {
      return window.screen.width <= 768 ? "90%" : "700px";
    },
  },
  watch: {
    isShow: function(val) {
      this.isShow_ = val;
    },
    isShow_: function(val) {
      if (!val) {
        this.$emit("close", { isShow: val });
      }
    },
  },
  methods: {
    copyTrigle() {
      this.$refs["ref-copy-btn"].click();
    },
    copy: function() {
      var clipboard = new Clipboard("#copy-btn");
      console.log(clipboard);
      // console.log(11)
      clipboard.on("success", () => {
        console.log("复制11成功");
        ElMessage.success("复制成功");
        // 释放内存
        clipboard.destroy();
      });
      clipboard.on("error", () => {
        // 不支持复制
        console.log("该浏览器不支持自动复制");
        // 释放内存
        clipboard.destroy();
      });
    },
  },
};
</script>

<style></style>
