const getters = {
  sysName: (state) => state.config.sysName,
  isProviderInjected: (state) => state.web3s.is_provider_injected,
  isEthereumEnabled: (state) => state.web3s.is_ethereum_enabled,
  isEthAddressGeted: (state) => state.web3s.is_eth_address_geted,
  addressEth: (state) => state.web3s.address_eth,
  blockNumber: (state) => state.web3s.block_number,
  web3: (state) => state.web3s.web3,
  // role
  tokenIdsWarrior: (state) => state.role_warrior.tokenIds,
  tokenIdsRobber: (state) => state.role_robber.tokenIds,
  tokenIdsMage: (state) => state.role_mage.tokenIds,
  tokenIdsYx: (state) => state.role_yx.tokenIds,
  balanceOfWarrior: (state) => state.role_warrior.balanceOf,
  tbalanceOfRobber: (state) => state.role_robber.balanceOf,
  balanceOfMage: (state) => state.role_mage.balanceOf,
  balanceOfYx: (state) => state.role_yx.balanceOf,

  heros: (state) => state.role_detail.heros,
  isHadHero: (state) => state.hero.isHadHero,
  isLoadingHero: (state) => state.hero.isLoadingHero,
  loadingProcessText: (state) => state.hero.loadingProcessText,

  nftPibnxBalance: (state) => state.pibnx.balanceOf,
  nftPibnxBalanceEffect: (state) => state.pibnx.balanceEffect,
  nftPibnxTokens: (state) => state.pibnx.tokens,
  nftPibnxPrice: (state) => state.pibnx.priceByEth,

  heroBalance: (state) => state.hero.balance,
  // config
  isLimited: (state) => state.config.isLimited,
  isShowQuery: (state) => state.config.isShowQuery,
  // isActionable: (state) => {
  //   if (!state.config.isLimited) {
  //     return true;
  //   }
  //   if (state.pibnx.balanceEffect * 100 >= state.hero.balance) {
  //     return false;
  //   }
  //   return false;
  // },
  // ===== ygg
  isOwner_ygg: (state) => state.ygg.isOwner,
  isOperator_ygg: (state) => state.ygg.isOperator,
  isPoolAdding_ygg: (state) => state.ygg.isPoolAdding,
  isPoolLoading_ygg: (state) => state.ygg.isPoolLoading,
  isApproving_ygg: (state) => state.ygg.isApproving,
  isApprovingFromHotAccount_ygg: (state) => state.ygg.isApprovingFromHotAccount,
  isApprovingFromHotAccountForShare_ygg: (state) =>
    state.ygg.isApprovingFromHotAccountForShare,
  isApprovingFromOperatorBeforSendReward_ygg: (state) =>
    state.ygg.isApprovingFromOperatorBeforSendReward,
  isApprovingFromOperatorBeforSendShareReward_ygg: (state) =>
    state.ygg.isApprovingFromOperatorBeforSendShareReward,
  isAddingOperator_ygg: (state) => state.ygg.isAddingOperator,
  isDeletingOperator_ygg: (state) => state.ygg.isDeletingOperator,
  isSendingReward_ygg: (state) => state.ygg.isSendingReward,
  isSendingRewardForShare_ygg: (state) => state.ygg.isSendingRewardForShare,
  isDepositing_ygg: (state) => state.ygg.isDepositing,
  isDepositingByOperator_ygg: (state) => state.ygg.isDepositingByOperator,
  isWithdrawing_ygg: (state) => state.ygg.isWithdrawing,
  isWithdrawingByOperator_ygg: (state) => state.yggisWithdrawingByOperator,
  isClaiming_ygg: (state) => state.ygg.isClaiming,
  isHadSharer_ygg: (state) => state.ygg.isHadSharer,
  addressOfSharer_ygg: (state) => state.ygg.addressOfSharer,
  operators_ygg: (state) => state.ygg.operators,
  pools_ygg: (state) => (isOnlyDepositedOrHadShareReward) => {
    let pools = [...state.ygg.pools];
    pools = pools.filter((pool) => {
      // console.log(
      //   `isOnlyDepositedOrHadShareReward[${isOnlyDepositedOrHadShareReward}] pool.isDepositedOrHadShareReward[${pool.isDepositedOrHadShareReward}]`
      // );
      if (!isOnlyDepositedOrHadShareReward) return true;

      return pool.isDepositedOrHadShareReward;
    });
    pools.sort((p1, p2) => {
      return parseInt(p1.sortIndex) - parseInt(p2.sortIndex);
    });
    return pools;
  },
  pool_ygg: (state) => (poolId) => state.ygg.pools[poolId],
  pool_req_deposit_ygg: (state) => (poolId) => state.ygg_req.reqDs[poolId],
  pool_req_withdraw_ygg: (state) => (poolId) => state.ygg_req.reqWs[poolId],
  pool_req_page_size_ygg: (state) => state.ygg_req.pageSize,
  pool_req_list_ygg: (state) => (poolId, isDeposit) => {
    if (isDeposit) {
      // console.log(
      //   `poolId[${poolId}] isDeposit[${isDeposit}]  reqsByPool[${state.ygg_req.reqDs[poolId]}]`,
      //   state.ygg_req.reqDs[poolId]
      // );
      return state.ygg_req.reqDs[poolId].reqsByPool;
    } else {
      return state.ygg_req.reqWs[poolId].reqsByPool;
    }
  },
  allowanceOfDepositToken_ygg: (state) => state.ygg.allowanceOfDepositToken,
  balanceOfDepositToken_ygg: (state) => state.ygg.balanceOfDepositToken,
  symbolOfDepositToken_ygg: (state) => state.ygg.symbolOfDepositToken,
};
export default getters;
