export default {
  namespaced: true,
  state: {
    addressContract: "0xbad89cf216fc26a73029bdad4642fb5373033277",
    pibnx: null,
    isContractAted: false,
    price: 100 * 10 ** 15,
    priceByEth: 0,
    amountMax: 20,
    balanceOf: 0,
    balanceEffect: 0,
    tokens: [],
    effectBlocks: 20 * 60 * 24 * 15,
  },
  mutations: {
    SET_PIBNX: (state, c) => {
      state.pibnx = c;
    },
    SET_IS_CONTRACT_ATED: (state, a) => {
      state.isContractAted = a;
    },
    SET_BALANCE_OF: (state, n) => {
      state.balanceOf = n;
    },
    SET_TOKENS: (state, t) => {
      state.tokens = t;
    },
    SET_BALANCE_EFFECT: (state, n) => {
      state.balanceEffect = n;
    },
    SET_PRICE_BY_ETH: (state, e) => {
      state.priceByEth = e;
    },
    SET_PRICE: (state, p) => {
      state.price = p;
    },
  },
  actions: {
    async contractAt({ rootState, state, commit }) {
      commit(
        "SET_PIBNX",
        new rootState.web3s.web3.eth.Contract(
          require("@/contracts/pibnx_abi"),
          state.addressContract
        )
      );
      commit("SET_IS_CONTRACT_ATED", true);
      const price = await state.pibnx.methods.price().call();
      commit("SET_PRICE", price);
      commit(
        "SET_PRICE_BY_ETH",
        rootState.web3s.web3.utils.fromWei(`${state.price}`)
      );
    },
    async balanceOf({ rootState, state, dispatch, commit }) {
      const sender = rootState.web3s.address_eth;
      if (!state.isContractAted) {
        await dispatch("contractAt");
      }
      if (!state.isContractAted) {
        return Promise.reject("contract ated from address and abi error");
      }
      const balanceOf = await state.pibnx.methods
        .balanceOf(sender)
        .call({ from: sender });
      console.log("balanceOf pibnx nft=", balanceOf);
      // clear state on change account
      commit("SET_BALANCE_OF", balanceOf);
      commit("SET_BALANCE_EFFECT", 0);
      commit("SET_TOKENS", []);
      if (balanceOf > 0) {
        const currBlock = await rootState.web3s.web3.eth.getBlockNumber();
        console.log("currBlock=", currBlock);
        let tokens = [];
        let balanceEffect = 0;
        for (let i = 0; i < balanceOf; i++) {
          const tokenId = await state.pibnx.methods
            .tokenOfOwnerByIndex(sender, i)
            .call({ from: sender });
          console.log("tokenId=", tokenId);
          const birthBlock = await state.pibnx.methods
            .birthBlock(tokenId)
            .call({ from: sender });
          console.log("birthBlock=", birthBlock);
          const remainBlock = state.effectBlocks - (currBlock - birthBlock);
          if (remainBlock > 0) {
            balanceEffect++;
          }
          const tokenURI = await state.pibnx.methods
            .tokenURI(tokenId)
            .call({ from: sender });
          console.log("tokenURI=", tokenURI);
          let tokenInfo = {
            tokenId: tokenId,
            birthBlock: birthBlock,
            remainBlock: remainBlock,
            tokenURI,
          };
          tokens.push(tokenInfo);
        }
        commit("SET_TOKENS", tokens);
        commit("SET_BALANCE_EFFECT", balanceEffect);
      }
    },
    async mint({ rootState, state, dispatch }, { amount }) {
      const sender = rootState.web3s.address_eth;
      if (sender == "" || sender == "0x") {
        console.log("mint pibnx error:no eth address logined");
        return;
      }
      if (!state.isContractAted) {
        await dispatch("contractAt");
      }
      if (!state.isContractAted) {
        console.log("mint pibnx error:contract ated error");
      }
      if (amount < 1 || amount > 20) {
        console.log("mint pibnx error:amount must in 1 to 20");
      }
      //   const needValue = amount * price;
      // not enough value

      const needValue =
        amount *
        parseFloat(rootState.web3s.web3.utils.fromWei(`${state.price}`));
      const balanceOf = await rootState.web3s.web3.eth.getBalance(sender);
      const balance = parseFloat(
        rootState.web3s.web3.utils.fromWei(`${balanceOf}`)
      );
      if (balance < needValue) {
        return Promise.reject("余额不足");
      }
      // return Promise.reject("comming soon");
      const web3 = rootState.web3s.web3;
      const needValueByWei = web3.utils.toWei(`${needValue}`);
      const gas = await state.pibnx.methods
        .mintByUser(amount)
        .estimateGas({ from: sender, value: needValueByWei });
      return await state.pibnx.methods
        .mintByUser(amount)
        .send(
          { from: sender, value: needValueByWei, gas: gas },
          (err, txHash) => {
            if (err) {
              console.log("mint error=", err);
            } else {
              console.log("mint txHash=", txHash);
            }
          }
        );
    },
  },
};
