const addressMapRoleType = {
  "0x22F3E436dF132791140571FC985Eb17Ab1846494": "战士",
  "0xaF9A274c9668d68322B0dcD9043D79Cd1eBd41b3": "盗贼",
  "0xC6dB06fF6e97a6Dc4304f7615CdD392a9cF13F44": "法师",
  "0xF31913a9C8EFE7cE7F08A1c08757C166b572a937": "游侠",
};
const state = {
  addressContract: "0x79961F74D1E53CA480e4dDf5675D5A6D7B1852e7",
  contractInstance: null,
  addressNewPlayerContract: "0x79961F74D1E53CA480e4dDf5675D5A6D7B1852e7",
  newPlayer: null,
  heros: [],
  heros_cache: [],
  // feel
  is_contract_ated: false,
};
const mutations = {
  SET_CONTRACT_INSTANCE: (state, c) => {
    state.contractInstance = c;
  },
  SET_NEW_PLAYER: (state, c) => {
    state.newPlayer = c;
  },
  SET_IS_CONTRACT_ATED: (state, a) => {
    state.is_contract_ated = a;
  },
  SET_HEROS: (state, h) => {
    state.heros = h;
  },
  ADD_HERO: (state, h) => {
    state.heros.push(h);
  },
  CLEAR_HEROS: (state) => {
    state.heros = [];
  },
  SET_HEROS_CACHE: (state, h) => {
    state.heros_cache = h;
  },
};
const actions = {
  async init({ dispatch }) {
    await dispatch("contractAt");
  },
  async contractAt({ rootGetters, state, commit }) {
    commit(
      "SET_CONTRACT_INSTANCE",
      new rootGetters.web3.eth.Contract(
        require("@/contracts/new_play_abi"),
        state.addressContract
      )
    );
    commit(
      "SET_NEW_PLAYER",
      new rootGetters.web3.eth.Contract(
        require("@/contracts/new_play_abi"),
        state.addressNewPlayerContract
      )
    );
    commit("SET_IS_CONTRACT_ATED", true);
    //console.log("role detail constract instance ated");
  },
  async getRoleDetail({ rootGetters, dispatch, state, commit }) {
    //console.log("getRoleDetail");
    if (state.is_contract_ated == false) {
      await dispatch("init");
    }
    commit("SET_HEROS_CACHE", []);

    // get not mining player info

    await dispatch("getRoleDetailByTokenIds", {
      tokenIds: rootGetters.tokenIdsWarrior,
      roleType: "战士",
    });

    await dispatch("getRoleDetailByTokenIds", {
      tokenIds: rootGetters.tokenIdsRobber,
      roleType: "盗贼",
    });
    await dispatch("getRoleDetailByTokenIds", {
      tokenIds: rootGetters.tokenIdsMage,
      roleType: "法师",
    });
    await dispatch("getRoleDetailByTokenIds", {
      tokenIds: rootGetters.tokenIdsYx,
      roleType: "游侠",
    });

    // get mining player info
    await dispatch("getMiningPlayerInfo");
    // switch buffer: set heros with cache and clear cache
    commit("SET_HEROS", state.heros_cache);
    commit("SET_HEROS_CACHE", []);
    // console.log(state.heros);
  },
  async getRoleDetailByTokenIds({ state, commit }, { tokenIds, roleType }) {
    // console.log(" getRoleDetailByTokenIds tokenIds=", tokenIds);
    if (tokenIds.length > 0) {
      let heros = [];
      for (let i = 0; i < tokenIds.length; i++) {
        const tokenId = tokenIds[i];
        const detail = await state.contractInstance.methods
          .getPlayerInfoBySet(tokenId)
          .call();
        detail.tokenId = tokenId;
        detail.roleType = roleType;
        detail.isWorking = false;
        // console.log(detail);
        heros.push(detail);
        commit(
          "hero/SET_LOADING_PROCESS_TEXT",
          `3-1:获得${roleType}英雄详情[${i}/${tokenIds.length}]`,
          { root: true }
        );
      }
      //   console.log(heros);
      heros = [...state.heros_cache, ...heros];
      commit("SET_HEROS_CACHE", heros);
    }
  },
  async getHeroDetailByTokenId(
    { rootState, state, commit, dispatch },
    { tokenId, roleType }
  ) {
    if (state.is_contract_ated == false) {
      await dispatch("init");
    }
    const detail = await state.contractInstance.methods
      .getPlayerInfoBySet(tokenId)
      .call();
    detail.tokenId = tokenId;
    detail.roleType = roleType;
    detail.isWorking = false;
    commit("ADD_HERO", detail);
    commit(
      "hero/SET_LOADING_PROCESS_TEXT",
      `2-5:获得休息中英雄信息[${state.heros.length}/${rootState.hero.balance}]`,
      { root: true }
    );
    dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
  },
  async getMiningHeroDetailByTokenId(
    { rootState, state, commit, dispatch },
    { tokenId }
  ) {
    console.log("getMiningHeroDetailByTokenId tokenId=", tokenId);
    if (state.is_contract_ated == false) {
      await dispatch("init");
    }
    const curr = await rootState.web3s.web3.eth.getBlockNumber();
    let detail = await state.contractInstance.methods
      .getPlayerInfoBySet(tokenId)
      .call();
    if (detail[1] == "0x0000000000000000000000000000000000000000") {
      detail = await state.newPlayer.methods.getPlayerInfoBySet(tokenId).call();
    }
    if (detail[1] == "0x0000000000000000000000000000000000000000") {
      console.log(
        "tokenId=",
        tokenId,
        `detail in ${state.addressContract}=`,
        detail
      );
      commit("hero/ADD_AMOUNT_FETCH_DETAIL_ERR", {}, { root: true });
      dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
      return;
    }
    detail.tokenId = tokenId;
    detail.roleType = addressMapRoleType[detail[1]];
    // get player work info
    const workInfo = await rootState.mining.contractInstance.methods
      .getPlayerWork(tokenId)
      .call();
    detail.workInfo = workInfo;
    // get income info
    const addressWorkType = workInfo.workType;

    let workContract = null;
    if (addressWorkType == rootState.work.addressDatie) {
      workContract = rootState.work.datie;
      detail.workTypeName = "打铁";
    } else if (addressWorkType == rootState.work.addressLinggong) {
      workContract = rootState.work.linggong;
      detail.workTypeName = "零工";
    } else if (addressWorkType == rootState.work.addressBulie) {
      workContract = rootState.work.bulie;
      detail.workTypeName = "捕猎";
    } else if (addressWorkType == rootState.work.addressTushu) {
      workContract = rootState.work.tushu;
      detail.workTypeName = "图书";
    } else if (addressWorkType == rootState.work.addressHunter) {
      workContract = rootState.work.hunter;
      detail.workTypeName = "酿酒";
    } else if (addressWorkType == rootState.work.addressBookmanager) {
      workContract = rootState.work.bookmanager;
      detail.workTypeName = "卷轴";
    } else if (addressWorkType == rootState.work.addressRangework) {
      workContract = rootState.work.rangework;
      detail.workTypeName = "打猎";
    } else if (addressWorkType == rootState.work.addressBlacksmith) {
      workContract = rootState.work.blacksmith;
      detail.workTypeName = "伐木";
    }
    let income;
    try {
      income = await workContract.methods
        .getIncome(detail[0], workInfo.startTime, curr)
        .call();
    } catch (err) {
      console.log(err);
      commit("hero/ADD_AMOUNT_FETCH_DETAIL_ERR", {}, { root: true });
      dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
      return;
    }
    detail.income = rootState.web3s.web3.utils.fromWei(income);
    detail.isWorking = true;
    detail.isMiningOld = true;
    commit("ADD_HERO", detail);
    commit(
      "hero/SET_LOADING_PROCESS_TEXT",
      `3-1:获得英雄旧工作信息[${state.heros.length}/${rootState.hero.balance}]`,
      { root: true }
    );
    dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
  },
  async getNewMiningHeroDetailByTokenId(
    { rootState, state, commit, dispatch },
    { tokenId }
  ) {
    if (state.is_contract_ated == false) {
      await dispatch("init");
    }
    const curr = await rootState.web3s.web3.eth.getBlockNumber();
    let detail = await state.contractInstance.methods
      .getPlayerInfoBySet(tokenId)
      .call();
    if (detail[1] == "0x0000000000000000000000000000000000000000") {
      detail = await state.newPlayer.methods.getPlayerInfoBySet(tokenId).call();
    }
    if (detail[1] == "0x0000000000000000000000000000000000000000") {
      commit("hero/ADD_AMOUNT_FETCH_DETAIL_ERR", {}, { root: true });
      dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
      return;
    }
    // console.log("new ming detail=", detail);
    detail.tokenId = tokenId;
    detail.roleType = addressMapRoleType[detail[1]];
    // get player work info
    const workInfo = await rootState.mining.contractInstance_new.methods
      .getPlayerWork(tokenId)
      .call();
    detail.workInfo = workInfo;
    console.log("working info=", workInfo);
    // get income info
    const addressWorkType = workInfo.workType;

    let workContract = null;
    if (addressWorkType == rootState.work.addressDatie) {
      workContract = rootState.work.datie;
      detail.workTypeName = "打铁";
    } else if (addressWorkType == rootState.work.addressLinggong) {
      workContract = rootState.work.linggong;
      detail.workTypeName = "零工";
    } else if (addressWorkType == rootState.work.addressBulie) {
      workContract = rootState.work.bulie;
      detail.workTypeName = "捕猎";
    } else if (addressWorkType == rootState.work.addressTushu) {
      workContract = rootState.work.tushu;
      detail.workTypeName = "图书";
    } else if (addressWorkType == rootState.work.addressHunter) {
      workContract = rootState.work.hunter;
      detail.workTypeName = "酿酒";
    } else if (addressWorkType == rootState.work.addressBookmanager) {
      workContract = rootState.work.bookmanager;
      detail.workTypeName = "卷轴";
    } else if (addressWorkType == rootState.work.addressRangework) {
      workContract = rootState.work.rangework;
      detail.workTypeName = "打猎";
    } else if (addressWorkType == rootState.work.addressBlacksmith) {
      workContract = rootState.work.blacksmith;
      detail.workTypeName = "伐木";
    }
    console.log("workContract =", workContract);
    let income;
    try {
      income = await workContract.methods
        .getIncome(detail[0], workInfo.startTime, curr)
        .call();
    } catch (err) {
      console.log(err);
      commit("hero/ADD_AMOUNT_FETCH_DETAIL_ERR", {}, { root: true });
      dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
      return;
    }

    detail.income = rootState.web3s.web3.utils.fromWei(income);
    detail.isWorking = true;
    detail.isMiningOld = false;
    commit("ADD_HERO", detail);
    commit(
      "hero/SET_LOADING_PROCESS_TEXT",
      `3-2:获得英雄新工作信息[${state.heros.length}/${rootState.hero.balance}]`,
      { root: true }
    );
    dispatch("hero/tryFinishLoadingHeros", {}, { root: true });
  },
  async getMiningPlayerInfo({ rootState, commit }) {
    const curr = await rootState.web3s.web3.eth.getBlockNumber();

    let heros = [];
    if (rootState.mining.balanceOf > 0) {
      for (let i = 0; i < rootState.mining.balanceOf; i++) {
        const tokenId = rootState.mining.tokenIds[i];
        let detail = await state.contractInstance.methods
          .getPlayerInfoBySet(tokenId)
          .call();
        if (detail[1] == "0x0000000000000000000000000000000000000000") {
          detail = await state.newPlayer.methods
            .getPlayerInfoBySet(tokenId)
            .call();
        }
        if (detail[1] == "0x0000000000000000000000000000000000000000") {
          console.log(
            "tokenId=",
            tokenId,
            `detail in ${state.addressContract}=`,
            detail
          );
          continue;
        }
        detail.tokenId = tokenId;
        detail.roleType = addressMapRoleType[detail[1]];
        // get player work info
        const workInfo = await rootState.mining.contractInstance.methods
          .getPlayerWork(tokenId)
          .call();
        detail.workInfo = workInfo;
        // get income info
        const addressWorkType = workInfo.workType;

        let workContract = null;
        if (addressWorkType == rootState.work.addressDatie) {
          workContract = rootState.work.datie;
          detail.workTypeName = "打铁";
        } else if (addressWorkType == rootState.work.addressLinggong) {
          workContract = rootState.work.linggong;
          detail.workTypeName = "零工";
        } else if (addressWorkType == rootState.work.addressBulie) {
          workContract = rootState.work.bulie;
          detail.workTypeName = "捕猎";
        } else if (addressWorkType == rootState.work.addressTushu) {
          workContract = rootState.work.tushu;
          detail.workTypeName = "图书";
        } else if (addressWorkType == rootState.work.addressHunter) {
          workContract = rootState.work.hunter;
          detail.workTypeName = "酿酒";
        } else if (addressWorkType == rootState.work.addressBookmanager) {
          workContract = rootState.work.bookmanager;
          detail.workTypeName = "卷轴";
        } else if (addressWorkType == rootState.work.addressRangework) {
          workContract = rootState.work.rangework;
          detail.workTypeName = "打猎";
        } else if (addressWorkType == rootState.work.addressBlacksmith) {
          workContract = rootState.work.blacksmith;
          detail.workTypeName = "伐木";
        }
        let income;
        try {
          income = await workContract.methods
            .getIncome(detail[0], workInfo.startTime, curr)
            .call();
        } catch (err) {
          console.log(err);
          continue;
        }
        detail.income = rootState.web3s.web3.utils.fromWei(income);
        detail.isWorking = true;
        detail.isMiningOld = true;
        heros.push(detail);
        commit(
          "hero/SET_LOADING_PROCESS_TEXT",
          `3-2:获得英雄旧工作信息[${i}/${rootState.mining.balanceOf}]`,
          { root: true }
        );
      }
    }
    if (rootState.mining.balanceOf_new > 0) {
      for (let i = 0; i < rootState.mining.balanceOf_new; i++) {
        const tokenId = rootState.mining.tokenIds_new[i];
        let detail = await state.contractInstance.methods
          .getPlayerInfoBySet(tokenId)
          .call();
        if (detail[1] == "0x0000000000000000000000000000000000000000") {
          detail = await state.newPlayer.methods
            .getPlayerInfoBySet(tokenId)
            .call();
        }
        if (detail[1] == "0x0000000000000000000000000000000000000000") {
          continue;
        }
        // console.log("new ming detail=", detail);
        detail.tokenId = tokenId;
        detail.roleType = addressMapRoleType[detail[1]];
        // get player work info
        const workInfo = await rootState.mining.contractInstance_new.methods
          .getPlayerWork(tokenId)
          .call();
        detail.workInfo = workInfo;
        console.log("working info=", workInfo);
        // get income info
        const addressWorkType = workInfo.workType;

        let workContract = null;
        if (addressWorkType == rootState.work.addressDatie) {
          workContract = rootState.work.datie;
          detail.workTypeName = "打铁";
        } else if (addressWorkType == rootState.work.addressLinggong) {
          workContract = rootState.work.linggong;
          detail.workTypeName = "零工";
        } else if (addressWorkType == rootState.work.addressBulie) {
          workContract = rootState.work.bulie;
          detail.workTypeName = "捕猎";
        } else if (addressWorkType == rootState.work.addressTushu) {
          workContract = rootState.work.tushu;
          detail.workTypeName = "图书";
        } else if (addressWorkType == rootState.work.addressHunter) {
          workContract = rootState.work.hunter;
          detail.workTypeName = "酿酒";
        } else if (addressWorkType == rootState.work.addressBookmanager) {
          workContract = rootState.work.bookmanager;
          detail.workTypeName = "卷轴";
        } else if (addressWorkType == rootState.work.addressRangework) {
          workContract = rootState.work.rangework;
          detail.workTypeName = "打猎";
        } else if (addressWorkType == rootState.work.addressBlacksmith) {
          workContract = rootState.work.blacksmith;
          detail.workTypeName = "伐木";
        }
        console.log("workContract =", workContract);
        let income;
        try {
          income = await workContract.methods
            .getIncome(detail[0], workInfo.startTime, curr)
            .call();
        } catch (err) {
          console.log(err);
          continue;
        }

        detail.income = rootState.web3s.web3.utils.fromWei(income);
        detail.isWorking = true;
        detail.isMiningOld = false;
        heros.push(detail);
        commit(
          "hero/SET_LOADING_PROCESS_TEXT",
          `3-2:获得英雄新工作信息[${i}/${rootState.mining.balanceOf_new}]`,
          { root: true }
        );
      }
    }
    if (heros.length > 0) {
      heros = [...state.heros_cache, ...heros];
      commit("SET_HEROS_CACHE", heros);
    }
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
