<script>
import { ElMessage } from "element-plus";
import { mapGetters } from "vuex";
import Req from "./req";
export default {
  name: "pool id",
  components: {
    Req,
  },
  props: {
    poolId: {
      type: Number,
      default: 0,
    },
  },
  emits: {
    deposit: null,
    withdraw: null,
  },
  data: function() {
    return {
      isExtend: false,
      amountOfSendReward: 0,
      amountOfSendRewardForShare: 0,
    };
  },

  computed: {
    ...mapGetters([
      "pool_ygg",
      "pool_req_deposit_ygg",
      "pool_req_withdraw_ygg",
      "web3",
      "isEthAddressGeted",
      "addressEth",
      "isApprovingFromHotAccount_ygg",
      "isApprovingFromHotAccountForShare_ygg",
      "isApprovingFromOperatorBeforSendReward_ygg",
      "isApprovingFromOperatorBeforSendShareReward_ygg",
      "isSendingReward_ygg",
      "isSendingRewardForShare_ygg",
      "isOperator_ygg",
      "isOwner_ygg",
      "isClaiming_ygg",
    ]),
    isShowOperatorPanel: function() {
      // console.log(
      //   `isOwner[${this.isOwner_ygg}]  isOperator[${this.isOperator_ygg}]   isExtend[${this.isExtend}]`
      // );
      let isSubPool = false;
      if (
        this.addressEth.toLowerCase() ==
          this.pool.yggColdAccount.toLowerCase() ||
        this.addressEth.toLowerCase() ==
          this.pool.yggHotAccount.toLowerCase() ||
        this.addressEth.toLowerCase() ==
          this.pool.yggHotAccountForShare.toLowerCase()
      ) {
        isSubPool = true;
      }
      // console.log("isSubPool=", isSubPool, "pool=", this.pool);
      return (
        (this.isOwner_ygg || this.isOperator_ygg || isSubPool) && this.isExtend
      );
    },
    pool: function() {
      return this.pool_ygg(this.poolId);
    },
    reqD: function() {
      return this.pool_req_deposit_ygg(this.poolId);
    },
    reqW: function() {
      return this.pool_req_withdraw_ygg(this.poolId);
    },
    poolName: function() {
      return this.pool.isHadGame2
        ? `${this.pool.nameOfGame1}-${this.pool.nameOfGame2}`
        : this.pool.nameOfGame1;
    },
    poolBips: function() {
      return this.pool.isHadGame2
        ? `${this.pool.bipsOfGame1}%-${this.pool.bipsOfGame2}%`
        : `${this.pool.bipsOfGame1}%`;
    },
    totalYield: function() {
      return (
        this.fromWei(`${this.pool.amountTotalOfRewardHis}`) +
        this.fromWei(`${this.pool.amountTotalOfRewardForShareHis}`)
      );
    },
    isYggHotAccount: function() {
      return (
        this.pool.yggHotAccount.toLowerCase() == this.addressEth.toLowerCase()
      );
    },
    isYggHotAccountForShare: function() {
      return (
        this.pool.yggHotAccountForShare.toLowerCase() ==
        this.addressEth.toLowerCase()
      );
    },
    amountOfDeposited: function() {
      return (
        parseFloat(this.fromWei(`${this.pool.amountOfDeposited}`)) +
        parseFloat(this.fromWei(`${this.reqD.balanceTotalsByUser}`)) +
        parseFloat(this.fromWei(`${this.reqW.balanceTotalsByUser}`))
      );
    },
  },
  watch: {
    isEthAddressGeted: async function(val) {
      // console.log(
      //   "poolId=",
      //   this.poolId,
      //   " isEthAddresssGeted val=",
      //   val,
      //   "  val_old=",
      //   val_old
      // );
      if (val) {
        try {
          await this.$store.dispatch("ygg/getPoolByIdFromChain", {
            poolId: this.poolId,
          });
          // console.log(this.pool);
          await this.$store.dispatch("ygg_req/getReqs", {
            poolId: this.poolId,
            isReqOfDeposit: true,
            pageNum: 0,
          });
          await this.$store.dispatch("ygg_req/getReqs", {
            poolId: this.poolId,
            isReqOfDeposit: false,
            pageNum: 0,
          });
        } catch (err) {
          console.log(err);
          ElMessage.error(err);
        }
      }
    },
  },
  methods: {
    onApprove: async function(tokenAddress, tag) {
      await this.$store
        .dispatch("ygg/approveToYGG", {
          poolId: this.poolId,
          tokenAddress,
          tag,
        })
        .then(() => {
          ElMessage.success("授权成功");
        })
        .catch((err) => {
          console.log(
            `approve pool[${this.poolId}] tokenAddress[${tokenAddress}] err[${err}]`
          );
          ElMessage.error(err);
        });
    },
    onClaim: async function() {
      await this.$store
        .dispatch("ygg/claimByUser", { poolId: this.poolId })
        .then(() => {
          ElMessage.success("成功");
        })
        .catch((err) => {
          ElMessage.error(`claim fail,err=${err}`);
        });
    },
    onInputFocus: function() {
      if (this.amountOfSendReward == 0) {
        this.amountOfSendReward = undefined;
      }
    },
    onInputBlur: function() {
      if (this.amountOfSendReward == undefined) {
        this.amountOfSendReward = 0;
      }
    },
    onInputFocus2: function() {
      if (this.amountOfSendRewardForShare == 0) {
        this.amountOfSendRewardForShare = undefined;
      }
    },
    onInputBlur2: function() {
      if (this.amountOfSendRewardForShare == undefined) {
        this.amountOfSendRewardForShare = 0;
      }
    },
    onSendReward: async function(isForShare) {
      if (!isForShare) {
        if (
          this.amountOfSendReward <= 0 ||
          this.amountOfSendReward >
            parseFloat(
              this.fromWei(
                `${this.pool.allowanceAndBalanceOfOperator.reward.allowance}`
              )
            ) ||
          this.amountOfSendReward >
            parseFloat(
              this.fromWei(
                `${this.pool.allowanceAndBalanceOfOperator.reward.balance}`
              )
            )
        ) {
          ElMessage.warning("发送金额大于余额或授权");
          return;
        }
      } else {
        if (
          this.amountOfSendRewardForShare <= 0 ||
          this.amountOfSendRewardForShare >
            parseFloat(
              this.fromWei(
                `${this.pool.allowanceAndBalanceOfOperator.rewardForShare.allowance}`
              )
            ) ||
          this.amountOfSendRewardForShare >
            parseFloat(
              this.fromWei(
                `${this.pool.allowanceAndBalanceOfOperator.rewardForShare.balance}`
              )
            )
        ) {
          ElMessage.warning("发送金额大于余额或授权");
          return;
        }
      }
      let amount = isForShare
        ? this.amountOfSendRewardForShare
        : this.amountOfSendReward;
      await this.$store
        .dispatch("ygg/sendReward", { poolId: this.poolId, amount, isForShare })
        .then(() => {
          ElMessage.success("发放成功");
        })
        .catch((err) => {
          ElMessage.error(`发放失败 err=${err}`);
        });
    },
    onDeposit: function() {
      console.log("action: click plus btn");
      this.$emit("deposit", { poolId: this.poolId });
    },
    onWithdraw: function() {
      this.$emit("withdraw", { poolId: this.poolId });
    },
    addressShorter: function(addr) {
      return `${addr.substr(0, 6)}...${addr.substr(-4, 4)}`;
    },
    fromWei: function(value) {
      if (
        value == null ||
        value == undefined ||
        value == "" ||
        value == 0 ||
        this.web3 == null
      ) {
        return 0;
      }
      return (
        (parseFloat(this.web3.utils.fromWei(`${value}`)).toFixed(4) * 10000) /
        10000
      );
    },
  },
};
</script>

