<template>
  <div class="ygg-list">
    <div class="ygg-only-deposit">
      <el-switch
        v-model="isOnlyDeposit"
        active-color="#C8AD84"
        inactive-color="#2E2A24"
      />
      <label style="margin-left:16px;">{{ onlyDepositText }}</label>
    </div>
    <div class="ygg-pools">
      <Item
        v-for="(p, index) in pools_ygg(isOnlyDeposit)"
        :key="index"
        :pool-id="p.poolId"
        @deposit="onDeposit"
        @withdraw="onWithdraw"
      ></Item>
    </div>
    <DepositDialog
      :is-show="isShowDepositDialog"
      :pool-id="poolIdOfDepositing"
      @close="onDepositDialogClose"
    />
    <WithdrawDialog
      :is-show="isShowWithdrawDialog"
      :pool-id="poolIdOfWithdrawing"
      @close="onWithdrawDialogClose"
    />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Item from "./item";
import DepositDialog from "./deposit";
import WithdrawDialog from "./withdraw";
export default {
  components: {
    Item,
    DepositDialog,
    WithdrawDialog,
  },
  data: function() {
    return {
      isOnlyDeposit: false,
      isShowDepositDialog: false,
      poolIdOfDepositing: -1,
      isShowWithdrawDialog: false,
      poolIdOfWithdrawing: -1,
    };
  },
  computed: {
    ...mapGetters(["pools_ygg"]),
    onlyDepositText: function() {
      return this.isOnlyDeposit ? "已参与" : "所有的";
    },
  },
  methods: {
    onDepositDialogClose: function() {
      this.isShowDepositDialog = false;
    },
    onWithdrawDialogClose: function() {
      this.isShowWithdrawDialog = false;
    },
    onDeposit: function({ poolId }) {
      console.log("emit receive: deposit: from pool item poolId=", poolId);
      this.poolIdOfDepositing = poolId;
      this.isShowDepositDialog = true;
    },
    onWithdraw: function({ poolId }) {
      this.poolIdOfWithdrawing = poolId;
      this.isShowWithdrawDialog = true;
    },
  },
};
</script>

