const state = {
  addressContract: "0xaF9A274c9668d68322B0dcD9043D79Cd1eBd41b3",
  contractInstance: null,
  balanceOf: 0,
  tokenIds: [],
  // feel
  is_contract_ated: false,
};
const mutations = {
  SET_CONTRACT_INSTANCE: (state, c) => {
    state.contractInstance = c;
  },
  SET_IS_CONTRACT_ATED: (state, a) => {
    state.is_contract_ated = a;
  },
  SET_BALANCE_OF: (state, b) => {
    state.balanceOf = b;
  },
  SET_TOKEN_IDS: (state, arr) => {
    state.tokenIds = arr;
  },
  ADD_TOKEN_ID: (state, t) => {
    state.tokenIds.push(t);
  },
  CLEAR_TOKEN_IDS: (state) => {
    state.tokenIds = [];
  },
};
const actions = {
  async init({ dispatch }) {
    // console.log("init role warrior", state);
    await dispatch("contractAt");
  },
  async contractAt({ rootGetters, state, commit }) {
    commit(
      "SET_CONTRACT_INSTANCE",
      new rootGetters.web3.eth.Contract(
        require("@/contracts/hero_abi"),
        state.addressContract
      )
    );
    commit("SET_IS_CONTRACT_ATED", true);
    // console.log("role warrior constract instance ated");
  },
  async balanceOf({ state, commit }, { owner }) {
    const balanceOf = await state.contractInstance.methods
      .balanceOf(owner)
      .call();
    // console.log("role robber balanceOf[", owner, "] =", balanceOf);
    commit("SET_BALANCE_OF", balanceOf);
  },
  async getHeroTokenIdsAsync({ state, commit, dispatch }, { owner }) {
    // clear tokenIds
    commit("SET_TOKEN_IDS", []);
    if (state.balanceOf <= 0) return;
    for (let i = 0; i < state.balanceOf; i++) {
      dispatch("getHeroTokenId", { owner, index: i });
    }
  },
  async getHeroTokenId({ state, commit, dispatch }, { owner, index }) {
    const tokenId = await state.contractInstance.methods
      .tokenOfOwnerByIndex(owner, index)
      .call();
    commit("ADD_TOKEN_ID", tokenId);
    commit(
      "hero/SET_LOADING_PROCESS_TEXT",
      `2-2:获得盗贼tokenId列表[${index}/${state.balanceOf}]`,
      { root: true }
    );
    // get hero detail
    dispatch(
      "role_detail/getHeroDetailByTokenId",
      {
        tokenId,
        roleType: "盗贼",
      },
      { root: true }
    );
  },
  async getRoleInfo({ state, commit }, { owner }) {
    if (state.balanceOf <= 0) return;
    const roleTotal = state.balanceOf;
    let tokenIds = [];
    for (let i = 0; i < roleTotal; i++) {
      const tokenId = await state.contractInstance.methods
        .tokenOfOwnerByIndex(owner, i)
        .call();
      //   console.log("role warrior [", i, "] name=", tokenId);
      tokenIds.push(tokenId);
      commit(
        "hero/SET_LOADING_PROCESS_TEXT",
        `2-2:获得盗贼tokenId列表[${i}/${roleTotal}]`,
        { root: true }
      );
    }
    commit("SET_TOKEN_IDS", tokenIds);
    //console.log("role robber  tokenIds=", tokenIds);
    // dispatch("role_detail/getRoleDetail", {}, { root: true });
  },
  async getHero({ state, dispatch }, { owner }) {
    if (!state.contractAt) {
      await dispatch("contractAt");
    }
    await dispatch("balanceOf", { owner });
    // await dispatch("getRoleInfo", { owner });
    dispatch("getHeroTokenIdsAsync", { owner });
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
