<template>
  <div class="ygg-head">
    <div class="ygg-logo"></div>
    <div class="link-dev-log" v-show="false">
      <router-link to="/ygg/devlog">DevLog</router-link>
    </div>
    <div class="ygg-connect-wallet">
      <el-button v-if="isEthAddressGeted" type="text" @click.prevent="onShare"
        >分享</el-button
      >
      <el-button
        type="text"
        class="connet-meta"
        style="color:white;"
        @click.prevent="connectWallet"
      >
        {{ connectWalletText }}</el-button
      >
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "ygg head",
  emits: {
    share: null,
  },
  computed: {
    ...mapGetters([
      "isProviderInjected",
      "isEthereumEnabled",
      "isEthAddressGeted",
      "addressEth",
    ]),
    connectWalletText: function() {
      if (this.isEthAddressGeted) {
        return `${this.addressEth.substr(0, 6)}...${this.addressEth.substr(
          -4,
          4
        )}`;
      } else {
        return "连接钱包";
      }
    },
  },
  methods: {
    connectWallet: function() {
      if (this.isEthAddressGeted) {
        return;
      }
      this.$store.dispatch("web3s/init");
    },
    onShare: function() {
      this.$emit("share");
    },
  },
};
</script>

